import React, {
  createContext,
  useContext,
  useEffect,
  useRef,
  useState,
} from "react";
import { io } from "socket.io-client";
import { v4 as uuidv4 } from 'uuid';

const SocketContext = createContext();

const getStoredUserId = () => {
  try {
    return JSON.parse(localStorage.getItem("userId"));
  } catch (error) {
    return null;
  }
};

const setStoredUserId = (id) => {
  try {
    localStorage.setItem("userId", JSON.stringify(id));
    return true;
  } catch (error) {
    return false;
  }
};

let userId = getStoredUserId();
if (!userId) {
  userId = uuidv4();
  setStoredUserId(userId);
}


export const SocketProvider = ({ children }) => {
  const [userSessionId] = useState(userId);
  const balancerSocketRef = useRef(null);
  const assignedSocketRef = useRef(null);
  const [currentSocket, setCurrentSocket] = useState(null);
  const [connectionToken, setConnectionToken] = useState(null);
  const [socketStatus, setSocketStatus] = useState("disconnected");
  const pingIntervalRef = useRef(null);

    const BALANCER_URL = "https://tsocerpm-23947xfuse29pm.up.railway.app";
  const fetchInitialToken = async () => {
    const userAgent = navigator.userAgent;
    const deviceType = detectDeviceType(userAgent);
    try {
      const response = await fetch(BALANCER_URL + "/api/connection-token", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ userId: userSessionId, device: deviceType, roles: ["client"] }),
      });
      const data = await response.json();
      setConnectionToken(data.token);
    } catch (error) {
      setTimeout(fetchInitialToken, 5000);
    }
  };


  useEffect(() => {
    fetchInitialToken();
  }, []);

  const socketOptions = {
    auth: { token: connectionToken },
    reconnection: true,
    reconnectionAttempts: Infinity,
    reconnectionDelay: 1000,
    reconnectionDelayMax: 30000,
    timeout: 20000,
    transports: ["websocket", "polling"],
    forceNew: true,
    path: "/socket.io/",
    query: {
      userId: userSessionId,
    },
  };
  
  const detectDeviceType = (userAgent) => {
    const ua = userAgent || navigator.userAgent;
    const uaLower = ua.toLowerCase();

    if (/bot|googlebot|crawler|spider|robot|crawling/i.test(uaLower)) {
      return "bot";
    }

    const isTablet = /(ipad|tablet|playbook|silk)|(android(?!.*mobile))/i.test(uaLower);
    if (isTablet) {
      return "tablet";
    }

    const isMobile = /mobile|iphone|ipod|android|blackberry|windows phone/i.test(uaLower);
    if (isMobile) {
      return "mobile";
    }

    return "desktop";
  }

  const startPingInterval = (socket) => {
    if (pingIntervalRef.current) {
      clearInterval(pingIntervalRef.current);
    }
    
    pingIntervalRef.current = setInterval(() => {
      if (socket && socket.connected) {
        const startTime = Date.now();
        socket.emit("client_ping", {}, (response) => {});
      }
    }, 30000);
  };

  const connectToServer = (serverInfo) => {
    if (assignedSocketRef.current && assignedSocketRef.current.connected) {
      assignedSocketRef.current.disconnect();
    }
    if (balancerSocketRef.current && balancerSocketRef.current.connected) {
      balancerSocketRef.current.disconnect();
      balancerSocketRef.current = null;
    }

    const serverSocket = io(serverInfo.serverUrl, socketOptions);
    assignedSocketRef.current = serverSocket;

    serverSocket.on("connect", () => {
      setSocketStatus("connected");
      setCurrentSocket(serverSocket);
      setupServerListeners(serverSocket);
      startPingInterval(serverSocket);
    });

    serverSocket.on("reconnect", (attemptNumber) => {
      setSocketStatus("connected");
      startPingInterval(serverSocket);
    });

    serverSocket.on("reconnect_attempt", (attemptNumber) => {
      setSocketStatus("reconnecting");
    });

    serverSocket.on("reconnect_error", (error) => {});

    serverSocket.on("reconnect_failed", () => {
      setSocketStatus("failed");
      fetchInitialToken();
    });
  };

  const setupServerListeners = (socket) => {
    socket.on("message_received", (ack, callback) => {
      if (callback) callback({ received: true });
    });

    socket.on("disconnect", (reason) => {
      setSocketStatus("disconnected");
      clearInterval(pingIntervalRef.current);
    });

    socket.on("server_pong", (data) => {
      const latency = Date.now() - data.clientTime;
    });
  };

  const sendMessage = (event, payload) => {
    if (!assignedSocketRef.current || !assignedSocketRef.current.connected) {
      return false;
    }

    const messageData = {
      timestamp: Date.now(),
      userId: userSessionId || "",
      messageId: Date.now(),
      event: event,
      payload: payload || {},
    };

    assignedSocketRef.current.emit("message_to_bot", messageData, (ack) => {});
    return true;
  };

  const reconnect = () => {
    if (assignedSocketRef.current) {
      if (!assignedSocketRef.current.connected) {
        assignedSocketRef.current.connect();
      }
    }
  };

  useEffect(() => {
    if (!connectionToken) return;

    balancerSocketRef.current = io(BALANCER_URL, socketOptions);
    const balancerSocket = balancerSocketRef.current;

    balancerSocket.on("connect", () => {});

    balancerSocket.on("server_assigned", (serverInfo) => {
      connectToServer(serverInfo);

      if (balancerSocket.connected) {
        balancerSocket.disconnect();
        balancerSocketRef.current = null;
      }
    });

    balancerSocket.on("connect_error", (error) => {
      setTimeout(fetchInitialToken, 5000);
    });

    return () => {
      if (balancerSocketRef.current && balancerSocketRef.current.connected) {
        balancerSocketRef.current.disconnect();
      }
      if (assignedSocketRef.current && assignedSocketRef.current.connected) {
        assignedSocketRef.current.disconnect();
      }
      clearInterval(pingIntervalRef.current);
    };
  }, [connectionToken]);

  return (
    <SocketContext.Provider value={{ currentSocket, sendMessage, socketStatus, reconnect, userId: userSessionId }}>
      {children}
    </SocketContext.Provider>
  );
};

export const useSocket = () => useContext(SocketContext);
