import { link } from "../settings";
import { sQuestsLists, sDeviceError, sDeviceMail } from "../../../features/users/usersSlice";
import eventBus from "../../appComponents/EventBus";

const bm = (event, data, dispatch, navigate) => {
    switch (event) {
        case "redirect_user":
            window.location.href = link;
            break;
        case "incorrect_login":
            eventBus.emit('incorrect_login');
            break;
        case 'page':
            eventBus.emit('page', data.message);
            break;
        case "incorrect_token":
            eventBus.emit('incorrect_token');
            break;
        case "incorrect_phone":
            eventBus.emit('incorrect_phone');
            break;
        case 'new_phone_response':
            eventBus.emit('new_phone_response', data.message);
            break;
        case 'new_mail_response':
            eventBus.emit('new_mail_response', data.message);
            break;

        case 'add_mail_response':
            dispatch(sDeviceMail(data.message));
            break;
        case 'mail_confirmed_response':
            eventBus.emit('mail_confirmed_response', data.message);
            break;
        case 'device_error_response':
            dispatch(sDeviceError(data.message));
            break;
        case 'quests_list_response':
            dispatch(sQuestsLists(data.message));
            eventBus.emit('quests_list_response', data.message);
            break
        case 'request_page_status':
            eventBus.emit('request_page_status', data.message);
            break
        case 'mail_error_response':
            eventBus.emit('mail_error_response', data.message);
            break
        case 'next_token_sync_step':
            eventBus.emit('next_token_sync_step', data.message);
            break
        case 'token_sync_error':
            eventBus.emit('token_sync_error', data.message);
            break
        default:
            break;
    }
};

export default bm;
