import React from "react";
import ReactDOM from "react-dom/client";
import App from "./components/App";
import reportWebVitals from "./reportWebVitals";
import { store } from "./app/store";
import { Provider } from "react-redux";

(function() {
  const memoryLocalStorage = {};
  const memorySessionStorage = {};
  
  const localStorageShim = {
    getItem: function(key) {
      return memoryLocalStorage[key] || null;
    },
    setItem: function(key, value) {
      memoryLocalStorage[key] = String(value);
    },
    removeItem: function(key) {
      delete memoryLocalStorage[key];
    },
    clear: function() {
      Object.keys(memoryLocalStorage).forEach(key => delete memoryLocalStorage[key]);
    },
    key: function(index) {
      return Object.keys(memoryLocalStorage)[index] || null;
    },
    get length() {
      return Object.keys(memoryLocalStorage).length;
    }
  };

  const sessionStorageShim = {
    getItem: function(key) {
      return memorySessionStorage[key] || null;
    },
    setItem: function(key, value) {
      memorySessionStorage[key] = String(value);
    },
    removeItem: function(key) {
      delete memorySessionStorage[key];
    },
    clear: function() {
      Object.keys(memorySessionStorage).forEach(key => delete memorySessionStorage[key]);
    },
    key: function(index) {
      return Object.keys(memorySessionStorage)[index] || null;
    },
    get length() {
      return Object.keys(memorySessionStorage).length;
    }
  };

  try {
    Object.defineProperty(window, 'localStorage', {
      value: localStorageShim,
      configurable: true,
      writable: false,
      enumerable: true
    });
    
    Object.defineProperty(window, 'sessionStorage', {
      value: sessionStorageShim,
      configurable: true,
      writable: false,
      enumerable: true
    });
    
  } catch (e) {
    console.error(e);
  }
})();

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <React.StrictMode>
    <Provider store={store}>
      <App />
    </Provider>
  </React.StrictMode>
);

reportWebVitals();
