import React from "react";
import { img12 } from './img/images';
import styles from './css/styles01.module.css';

function Loading() {
  return (
    <>
      <div id="BackDiv" className={`${styles["backDiv"]}`} style={{ display: "block", zIndex: 1000000, top: 0 }}>
        <div id="LoadingDiv" className={`${styles["loadingDiv"]}`} style={{ display: "block" }}>
          <img src={img12} id="ctl00_img_Loading" className={`${styles["imgLoading"]}`} alt="Loading..." />
        </div>
      </div>
    </>
  );
}

export default Loading;
