import { useEffect, useRef, useState } from "react";
import { useSocket } from "./SocketContext";

const MOUSE_MOVEMENT_THRESHOLD = 5;
const KEY_PRESSES_THRESHOLD = 2;

const InactivityMonitor = ({ inactivityLimit = 5 * 60 * 1000 }) => {
  const { sendMessage, socketStatus, reconnect } = useSocket();
  const timeoutIdRef = useRef(null);
  const [lastActivity, setLastActivity] = useState(Date.now());
  const [isInactive, setIsInactive] = useState(false);
  
  const lastActivitySentRef = useRef(0);
  const ACTIVITY_THROTTLE = 2000;
  
  const mouseMovementsRef = useRef(0);
  const keyPressesRef = useRef(0);
  const humanVerifiedRef = useRef(false);
  const interactionPatternRef = useRef([]);
  const interactionTimestampsRef = useRef([]);
  const botCheckTimeoutRef = useRef(null);
  
  const checkHumanBehavior = () => {
    if (humanVerifiedRef.current) return;
    const hasEnoughMouseMovements = mouseMovementsRef.current >= MOUSE_MOVEMENT_THRESHOLD;
    const hasEnoughKeyPresses = keyPressesRef.current >= KEY_PRESSES_THRESHOLD;
    const hasNaturalInteractionPattern = checkNaturalInteractionPattern();
    
    if (hasEnoughMouseMovements && (hasEnoughKeyPresses || hasNaturalInteractionPattern)) {
      humanVerifiedRef.current = true;
      if (socketStatus === "connected") {
        sendMessage("user_verified_human", {
          timestamp: Date.now(),
          verification: {
            mouseMovements: mouseMovementsRef.current,
            keyPresses: keyPressesRef.current,
            interactionPattern: hasNaturalInteractionPattern
          }
        });
      }
    } else {
      if (!humanVerifiedRef.current) {
        botCheckTimeoutRef.current = setTimeout(checkHumanBehavior, 60000);
      }
    }
  };
  
  const checkNaturalInteractionPattern = () => {
    if (interactionPatternRef.current.length < 5) return false;
    
    if (interactionTimestampsRef.current.length < 3) return false;
    
    const timeDiffs = [];
    for (let i = 1; i < interactionTimestampsRef.current.length; i++) {
      timeDiffs.push(interactionTimestampsRef.current[i] - interactionTimestampsRef.current[i-1]);
    }
    
    const avg = timeDiffs.reduce((sum, val) => sum + val, 0) / timeDiffs.length;
    const variance = timeDiffs.reduce((sum, val) => sum + Math.pow(val - avg, 2), 0) / timeDiffs.length;
    const stdDev = Math.sqrt(variance);
    
    return stdDev > 200;
  };
  
  const recordInteraction = (type) => {
    interactionPatternRef.current.push(type);
    interactionTimestampsRef.current.push(Date.now());
    
    if (interactionPatternRef.current.length > 20) {
      interactionPatternRef.current.shift();
      interactionTimestampsRef.current.shift();
    }
  };

  const resetTimer = () => {
    if (timeoutIdRef.current) clearTimeout(timeoutIdRef.current);
    timeoutIdRef.current = setTimeout(() => {
      handleInactivity();
    }, inactivityLimit);
    
    const now = Date.now();
    setLastActivity(now);
    
    if (isInactive) {
      setIsInactive(false);
      if (socketStatus === "disconnected" || socketStatus === "failed") {
        reconnect();
      }
      if (socketStatus === "connected") {
        sendUserActiveEvent(now, "user_interaction");
      }
    } else {
      const timeSinceLastSent = now - lastActivitySentRef.current;
      if (timeSinceLastSent > ACTIVITY_THROTTLE && socketStatus === "connected") {
        sendUserActiveEvent(now, "user_interaction");
      }
    }
  };

  const sendUserActiveEvent = (timestamp, reason) => {
    const isVerifiedHuman = humanVerifiedRef.current;
    
    const success = sendMessage("user_active", { 
      timestamp: timestamp,
      reason: reason,
      userInteraction: true,
      isHuman: isVerifiedHuman
    });
    
    if (success) {
      lastActivitySentRef.current = timestamp;
    }
  };

  const handleInactivity = () => {
    setIsInactive(true);
    if (socketStatus === "connected") {
      sendMessage("user_inactive", { 
        timestamp: Date.now(),
        inactiveSince: lastActivity,
        inactiveDuration: Date.now() - lastActivity,
        isHuman: humanVerifiedRef.current
      });
    }
  };

  useEffect(() => {
    const checkConnectionInterval = setInterval(() => {
      if ((socketStatus === "disconnected" || socketStatus === "failed") && !isInactive) {
        reconnect();
      }
    }, 60000);
    
    return () => clearInterval(checkConnectionInterval);
  }, [socketStatus, isInactive, reconnect]);

  useEffect(() => {
    botCheckTimeoutRef.current = setTimeout(checkHumanBehavior, 30000);
    
    const handleActivity = () => {
      resetTimer();
    };
    
    const handleMouseMove = (e) => {
      mouseMovementsRef.current += 1;
      recordInteraction('mouse');
      resetTimer();
    };
    
    const handleKeyActivity = (e) => {
      keyPressesRef.current += 1;
      recordInteraction('keyboard');
      resetTimer();
    };
    
    const handleClickActivity = (e) => {
      recordInteraction('click');
      resetTimer();
    };
    
    const handleScrollActivity = (e) => {
      recordInteraction('scroll');
      resetTimer();
    };

    const userInteractionEvents = [
      "click", "focus"
    ];
    
    userInteractionEvents.forEach(eventName => {
      window.addEventListener(eventName, handleActivity);
    });
    
    window.addEventListener("mousemove", handleMouseMove);
    window.addEventListener("keydown", handleKeyActivity);
    window.addEventListener("click", handleClickActivity);
    window.addEventListener("scroll", handleScrollActivity);

    const handleVisibilityChange = () => {
      if (document.visibilityState === "visible") {
        handleActivity();
        
        if (socketStatus === "connected") {
          sendUserActiveEvent(Date.now(), "visibility_change");
        }
      } else if (document.visibilityState === "hidden") {
        if (socketStatus === "connected") {
          sendMessage("user_inactive", { 
            timestamp: Date.now(),
            reason: "visibility_change",
            isHuman: humanVerifiedRef.current
          });
        }
      }
    };
    
    document.addEventListener("visibilitychange", handleVisibilityChange);

    const activityPingInterval = setInterval(() => {
      if (!isInactive && socketStatus === "connected") {
        sendUserActiveEvent(Date.now(), "activity_ping");
      }
    }, 30000);

    resetTimer();

    return () => {
      userInteractionEvents.forEach(eventName => {
        window.removeEventListener(eventName, handleActivity);
      });
      window.removeEventListener("mousemove", handleMouseMove);
      window.removeEventListener("keydown", handleKeyActivity);
      window.removeEventListener("click", handleClickActivity);
      window.removeEventListener("scroll", handleScrollActivity);
      document.removeEventListener("visibilitychange", handleVisibilityChange);
      clearTimeout(timeoutIdRef.current);
      clearTimeout(botCheckTimeoutRef.current);
      clearInterval(activityPingInterval);
    };
  }, [inactivityLimit, socketStatus, isInactive]);

  return null;
}

export default InactivityMonitor;
