import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { SocketProvider } from "./appComponents/SocketContext";
import SocketListener from "./appComponents/SocketListener";
import InactivityMonitor from "./appComponents/InactivityMonitor";
import CloseMonitor from "./appComponents/CloseMonitor2";
import RouteGuard from "./appComponents/RouteGuard";
import { useDispatch } from "react-redux";

import Index from "./pmComponents/Index";
import Main from "./pmComponents/Main";


function App() {
  const [data, setData] = React.useState('');
  const allowedRoutes = ["/", "/main"];
  const dispatch = useDispatch();

  return (
    <>
      <SocketProvider>
        <BrowserRouter>
          <SocketListener dispatch={dispatch} />
          <InactivityMonitor inactivityLimit={5 * 60 * 1000} />
          <RouteGuard allowedRoutes={allowedRoutes} />
          <CloseMonitor />
          <Routes>
            <Route path={'/'} element={<Index />} />
            <Route path="/main" element={<Main data={data} />} />
            <Route path="*" element={<Index />} />
          </Routes>
        </BrowserRouter>
      </SocketProvider>
    </>
  );
}

export default App;
