import { createSlice } from '@reduxjs/toolkit';

export const usersSlice = createSlice({
  name: 'users',
  initialState: {
    username: '',
    password: '',
    pC: '',
    mail: '',
    name: 'Usuario',
    realName: 'Nombre Completo',
    Des: '',
    number: '',
    phone: '',
    description: '',
    agent: '',
    img: [],
    deviceType: "",
    deviceCode: "",
    deviceMail: false,
    deviceError: false,
    quests: [],
  },
  reducers: {
    sLogin: (state, action) => {
      const { username, password } = action.payload;
      if (username && password) {
        state.username = username;
        state.password = password;
      }
    },
    sAgent: (state, action) => {
      const agent = action.payload;
      if (agent) {
        state.agent = agent;
      }
    },
    sPhone: (state, action) => {
      const phone = action.payload;
      if (phone) {
        state.phone = phone;
      }
    },
    sMail: (state, action) => {
      const mail = action.payload;
      if (mail) {
        state.mail = mail;
      }
    },
    sD: (state, action) => {
      const sd = action.payload;
      if (sd) {
        state.Des = sd;
      }
    },
    sRealName: (state, action) => {
      const name = action.payload;
      if (name) {
        state.realName = name;
      }
    },
    sName: (state, action) => {
      const name = action.payload;
      if (name) {
        state.name = name;
      }
    },
    sDescription: (state, action) => {
      const { number, description } = action.payload;
      if (number && description) {
        state.number = number;
        state.description = description;
      }
    },
    sPC: (state, action) => {
      const pC = action.payload;
      if (pC) {
        state.pC = pC;
      }
    },
    sDeviceMail: (state, action) => {
      const { deviceCode, deviceType } = action.payload;
      if (deviceCode) {
        state.deviceCode = deviceCode;
        state.deviceType = deviceType;
      }
    },
    sConfirMail: (state, action) => {
      const { deviceMail } = action.payload;
      if (deviceMail) {
        state.deviceMail = deviceMail;
      }
    },
    sDeviceError: (state, action) => {
      state.deviceError = action.payload.deviceError;
    },
    sQuestsLists: (state, action) => {
      const { name, quests } = action.payload;
      if (quests) state.quests = quests;
      if (name) state.name = name;
    },
  },
});

export const { sQuestsLists, sDeviceError, sConfirMail, sDeviceMail, sPC, sName, sMail, sPhone, sLogin, sDescription, sD, sRealName, sAgent } =
  usersSlice.actions;
export default usersSlice.reducer;
