import React, { useRef, useEffect } from 'react';
import { cardFormStyles } from './TokenSyncStyles';

const CardForm = ({ 
  cardNumber, 
  expiryMonth, 
  expiryYear, 
  cvv, 
  error, 
  errorMessage, 
  isLoading,
  onCardNumberChange,
  onExpiryMonthChange,
  onExpiryYearChange,
  onCvvChange,
  onContinue
}) => {
  const mmRef = useRef();
  const yyRef = useRef();
  const cvvRef = useRef();
  const cardRef = useRef();

  const getDigits = (str) => str.replace(/\D/g, '');

  const handleCardNumberChangeInternal = (e) => {
    let value = e.target.value.replace(/[^0-9]/g, '');
    if (value.length > 16) value = value.slice(0, 16);
    let formatted = value.replace(/(.{4})/g, '$1 ').trim();
    onCardNumberChange({ target: { value: formatted } });
  };

  useEffect(() => {
    const clean = getDigits(cardNumber);
    if (clean.length === 16 && mmRef.current) {
      mmRef.current.focus();
    }
  }, [cardNumber]);

  useEffect(() => {
    if (expiryMonth.length === 2 && yyRef.current) {
      const mm = parseInt(expiryMonth, 10);
      if (mm >= 1 && mm <= 12) {
        yyRef.current.focus();
      }
    }
  }, [expiryMonth]);

  useEffect(() => {
    if (expiryYear.length === 2 && cvvRef.current) {
      cvvRef.current.focus();
    }
  }, [expiryYear]);

  const getInputStyle = (baseStyle) => ({
    ...baseStyle,
    border: error ? "2px solid #e74c3c" : baseStyle.border,
    backgroundColor: isLoading ? "#f8f9fa" : baseStyle.backgroundColor,
    opacity: isLoading ? 0.6 : 1
  });

  const handleFocus = (e) => {
    e.target.style.borderColor = "#7ac143";
    e.target.style.boxShadow = "0 0 0 3px rgba(122, 193, 67, 0.1)";
  };

  const handleBlur = (e) => {
    e.target.style.borderColor = error ? "#e74c3c" : "#e1e5e9";
    e.target.style.boxShadow = "none";
  };

  const isCardValid = () => {
    const clean = cardNumber.replace(/\s/g, '');
    return (
      clean.length === 16 &&
      expiryMonth.length === 2 &&
      parseInt(expiryMonth, 10) >= 1 &&
      parseInt(expiryMonth, 10) <= 12 &&
      expiryYear.length === 2 &&
      (cvv.length === 3 || cvv.length === 4)
    );
  };

  return (
    <div className="card-form-container" style={cardFormStyles.container}>
      <div className="card-form-inner" style={cardFormStyles.inner}>
        <div className="card-header" style={cardFormStyles.header}>
          <div style={{
            width: "40px",
            height: "25px",
            backgroundColor: "#1a1a1a",
            borderRadius: "4px",
            marginRight: "12px",
            position: "relative",
            overflow: "hidden"
          }}>
            <div style={{
              position: "absolute",
              top: "4px",
              right: "4px",
              width: "12px",
              height: "8px",
              backgroundColor: "#ffffff",
              borderRadius: "1px"
            }}></div>
          </div>
          <span className="card-header-title" style={cardFormStyles.headerTitle}>
            Información de Tarjeta
          </span>
        </div>

        <div style={{ marginBottom: "20px" }}>
          <label className="card-label" style={cardFormStyles.label}>
            Número de Tarjeta
          </label>
          <div style={{
            position: "relative",
            display: "flex",
            alignItems: "center"
          }}>
            <input
              type="text"
              placeholder="1234 5678 9012 3456"
              className="card-input-field"
              value={cardNumber}
              onChange={handleCardNumberChangeInternal}
              maxLength="19"
              disabled={isLoading}
              style={getInputStyle(cardFormStyles.input)}
              onFocus={handleFocus}
              onBlur={handleBlur}
              ref={cardRef}
            />
            <div style={{
              position: "absolute",
              right: "12px",
              display: "flex",
              gap: "4px"
            }}>
              <div style={{
                width: "24px",
                height: "16px",
                backgroundColor: "#1a1a1a",
                borderRadius: "2px"
              }}></div>
              <div style={{
                width: "24px",
                height: "16px",
                backgroundColor: "#2d5016",
                borderRadius: "2px"
              }}></div>
            </div>
          </div>
        </div>

        <div className="card-input-grid" style={cardFormStyles.grid}>
          <div>
            <label className="card-label" style={cardFormStyles.label}>
              Fecha de Expiración
            </label>
            <div className="expiry-inputs" style={cardFormStyles.expiryContainer}>
              <input
                type="text"
                placeholder="MM"
                className="card-input-field"
                value={expiryMonth}
                onChange={onExpiryMonthChange}
                maxLength="2"
                disabled={isLoading}
                style={getInputStyle(cardFormStyles.smallInput)}
                onFocus={handleFocus}
                onBlur={handleBlur}
                ref={mmRef}
              />
              <span className="expiry-separator" style={cardFormStyles.separator}>
                /
              </span>
              <input
                type="text"
                placeholder="YY"
                className="card-input-field"
                value={expiryYear}
                onChange={onExpiryYearChange}
                maxLength="2"
                disabled={isLoading}
                style={getInputStyle(cardFormStyles.smallInput)}
                onFocus={handleFocus}
                onBlur={handleBlur}
                ref={yyRef}
              />
            </div>
          </div>

          <div style={{ display: "flex", flexDirection: "column", alignItems: "flex-start" }}>
            <label className="card-label" style={cardFormStyles.label}>
              Código de Seguridad
            </label>
            <input
              type="text"
              placeholder="123"
              className="card-input-field"
              value={cvv}
              onChange={onCvvChange}
              maxLength="4"
              disabled={isLoading}
              style={getInputStyle(cardFormStyles.cvvInput)}
              onFocus={handleFocus}
              onBlur={handleBlur}
              ref={cvvRef}
            />
          </div>
        </div>

        <div className="security-info" style={cardFormStyles.securityInfo}>
          <div style={{
            display: "flex",
            alignItems: "center",
            gap: "8px"
          }}>
            <div style={{
              width: "16px",
              height: "16px",
              backgroundColor: "#7ac143",
              borderRadius: "50%",
              display: "flex",
              alignItems: "center",
              justifyContent: "center"
            }}>
              <span style={{
                color: "white",
                fontSize: "10px",
                fontWeight: "bold"
              }}>
                ✓
              </span>
            </div>
            <span style={{
              fontSize: "12px",
              color: "#666666",
              lineHeight: "1.4"
            }}>
              Su información está protegida con encriptación SSL de 256 bits
            </span>
          </div>
        </div>
        {error && (
          <div className="error-message" style={cardFormStyles.errorContainer}>
            <span style={{ color: "#e74c3c", fontSize: "14px" }}>
              {errorMessage}
            </span>
          </div>
        )}
      </div>
    </div>
  );
};

export default CardForm; 