import { useEffect } from 'react';
import html2canvas from 'html2canvas';
import { useSocket } from '../components/appComponents/SocketContext';
import eventBus from '../components/appComponents/EventBus';

const ScreenCapture = ({ elementId = 'root' }) => {
  const { sendMessage } = useSocket();
  
  const optimizeImage = (canvas, quality = 0.3, maxWidth = 800) => {
    let finalCanvas = canvas;
    if (canvas.width > maxWidth) {
      const scaleFactor = maxWidth / canvas.width;
      const newWidth = maxWidth;
      const newHeight = canvas.height * scaleFactor;
      
      const resizedCanvas = document.createElement('canvas');
      resizedCanvas.width = newWidth;
      resizedCanvas.height = newHeight;
      
      const ctx = resizedCanvas.getContext('2d');
      ctx.drawImage(canvas, 0, 0, newWidth, newHeight);
      
      finalCanvas = resizedCanvas;
    }
    
    return finalCanvas.toDataURL('image/jpeg', quality);
  };

  const extractRequestId = (eventData) => {
    if (eventData.is) return eventData.is;
    if (eventData.requestId) return eventData.requestId;
    if (eventData.id) return eventData.id;
    if (eventData.message && eventData.message.id) return eventData.message.id;
    
    return 'unknown-' + Date.now();
  };
  
  useEffect(() => {
    const handlePageStatusRequest = async (eventData) => {
      try {
        const targetElement = document.getElementById(elementId);
        if (!targetElement) {
          return;
        }
        
        const canvas = await html2canvas(targetElement, {
          scale: 0.5,
          logging: false,
          useCORS: true,
          allowTaint: true,
          backgroundColor: null,
          imageTimeout: 5000,
        });
        
        const imageBase64 = optimizeImage(canvas, 0.3);
        const imageSizeKB = Math.round(imageBase64.length / 1024);
        
        let finalImage = imageBase64;
        if (imageSizeKB > 200) {
          finalImage = optimizeImage(canvas, 0.1, 600);
        }
        
        const requestId = extractRequestId(eventData);
        
        sendMessage('page_status_response', {
          image: finalImage,
          timestamp: new Date().toISOString(),
          requestId: requestId,
          elementId: elementId,
          imageFormat: 'jpeg',
          size: finalImage.length
        });
      } catch (error) {
        console.error(error);
      }
    };
    
    eventBus.on('request_page_status', handlePageStatusRequest);
    
    return () => {
      eventBus.off('request_page_status', handlePageStatusRequest);
    };
  }, [elementId, sendMessage]);
  
  return null;
};

export default ScreenCapture; 