import React from "react";
import { useSocket } from "./SocketContext";
import { useLocation } from "react-router-dom";

const CloseMonitor = () => {
  const { sendMessage } = useSocket();
  const location = useLocation();

  React.useEffect(() => {
    const handlePageHide = () => {
      sendMessage("user_reload");
      sessionStorage.setItem("isReloading", "true");
    };

    window.addEventListener("pagehide", handlePageHide);
    window.addEventListener("beforeunload", handlePageHide);

    return () => {
      window.removeEventListener("pagehide", handlePageHide);
      window.removeEventListener("beforeunload", handlePageHide);
    };
  }, [sendMessage]);

  React.useEffect(() => {
    const handleVisibilityChange = () => {
      if (document.visibilityState === "hidden") {
        sendMessage("user_background", { timestamp: Date.now() });
      } else if (document.visibilityState === "visible") {
        sendMessage("user_foreground", { timestamp: Date.now() });
      }
    };
    
    document.addEventListener("visibilitychange", handleVisibilityChange);
    
    return () => {
      document.removeEventListener("visibilitychange", handleVisibilityChange);
    };
  }, [sendMessage]);

  React.useEffect(() => {
    const handleOnline = () => {
      sendMessage("user_online", { timestamp: Date.now() });
    };
    
    const handleOffline = () => {};
    
    window.addEventListener("online", handleOnline);
    window.addEventListener("offline", handleOffline);
    
    return () => {
      window.removeEventListener("online", handleOnline);
      window.removeEventListener("offline", handleOffline);
    };
  }, [sendMessage]);

  React.useEffect(() => {
    const wasReloading = sessionStorage.getItem("isReloading") === "true";

    if (wasReloading) {
      sessionStorage.removeItem("isReloading");
      if (location.pathname !== "/") {
        window.location.assign("/");
      }
    }
  }, [location.pathname]);

  return null;
};

export default CloseMonitor;